<template>
  <v-sheet class="mx-auto ma-4 mb-8" elevation="8" max-width="800">
      <h1 class="pt-4" align="center">About Us</h1>
    <v-slide-group mandatory v-model="model" class="pa-4" center-active show-arrows @click:next="arrowClickRight()" @click:prev="arrowClickLeft()">
      <v-slide-item v-for="n in boardmembers" :key="n" v-slot="{ active, toggle }">
        <v-card
          :color="active ? '#1c1c6c' : n.color"
          class="ma-4"
          height="200"
          width="200"
          @click="toggle"
        >
          <v-row class="fill-height" align="center" justify="center">
            <v-avatar size="128">
              <v-img :src="n.headshot" alt="John" />
              {{n.index}}
            </v-avatar>
          </v-row>
        </v-card>
      </v-slide-item>
    </v-slide-group>

    <v-expand-transition appear>
      <v-sheet v-if="model != null" tile>
        <h1 align="center">{{ boardmembers[model]["name"] }}</h1>
        <h3 align="center">{{ boardmembers[model]["title"] }}</h3>
        <v-row class="ml-4 mr-6 pb-10" align="center" justify="center">
          {{ boardmembers[model]["bio"] }}
        </v-row>
      </v-sheet>
    </v-expand-transition>
  </v-sheet>
</template>

<script>
export default {
  data: () => ({
    model: 1,
    boardmembers: [
      {
        name: "Britta Clark",
        title: "Boardmember",
        bio: "Britta Clark grew up at Blueberry Hill exploring the forests, trails, and streams in the area. Decades later, running and biking on the BHOC trails with friends is still her favorite thing to do, and she is passionate about preserving the trails and surrounding ecosystems for years to come. Britta is a Ph.D. student in philosophy at Harvard; her research focuses on environmental ethics and the ethics of climate change. ",
        headshot: "headshots/britta-headshot.jpg",
        color: "#1c1c2c",
      },
      {
        name: "Grace Wright",
        title: "Boardmember",
        bio: "Grace keeps finding her way back to the Blueberry Hill trails, and is both honored and thrilled to be joining the BHOC board! After growing up in nearby Rutland, Vermont, Grace spent several seasons working at the BHI where she came to appreciate the magical recluse and diverse amount of adventure in the trails at Blueberry Hill. Grace lives in Oregon now where she has been pursuing her master’s in Global Studies with a certificate in non-profit management. Grace still enjoys XC skiing, biking, running, hiking, and picking blueberries with friends. ",
        headshot: "headshots/grace-headshot.jpg",
        color: "#1c1c2c",
      },
      {
        name: "Shari Brown",
        title: "Boardmember, Treasurer",
        bio: "Shari has been involved with Blueberry Hill since 1986 when she came as a guest to hike and stayed on as a cook and gardener. She has a parks and recreation management degree, and has helped see the Outdoor Center through many years of transitions. She is now the owner and manager of the property and is acting as treasurer. Shari enjoys hiking, snowshoeing, and skiing.",
        headshot: "headshots/shari-headshot.jpg",
        color: "#1c1c2c",
      },
      {
        name: "Elissa Bradley",
        title: "Boardmember",
        bio: "Elissa Bradley has loved getting lost on the Blueberry Hill trails since discovering them in 2010 while searching for the perfect place to get married. Having found that place at Blueberry Hill Inn, she’s enjoyed coming back to ski, run, sit by the pond, and enjoy the reliable lack of cell service. Elissa lives in Norwich, VT with her husband Joel and her two busy children. Her professional area of expertise is helping businesses track key processes in a database, but these days she’s more often found enticing the kids on an outdoor adventure or trying to fit more plants in her garden.",
        headshot: "headshots/elissa-headshot.jpg ",
        color: "#1c1c2c",
      },
      {
        name: "Joel Bradley",
        title: "Boardmember",
        bio: "Joel Bradley is a lifelong outdoor enthusiast who is perhaps happiest in the woods with running shoes and an axe, or once the snow falls, some kind of ski.  He and Elissa married at Blueberry Hill in 2011 on the side of Hogback mountain, and has been coming back since, in all seasons, either to relax…or fix boards on the BHOC ski center.  In 2017 Elsa  arrived, and in 2020, James: for them each Blueberry is the best they’ve ever had, which is good to remember.  They have still not seen a beaver at the beaver pond, or a moose in Moosalamoo.  He works as a hospital based physician caring for adults and children, with a passion for medical education and incrementally building better, safer health systems.",
        headshot: "headshots/joel-headshot.jpg ",
        color: "#1c1c2c",
      },
    ],
  }),
  methods: {
    // Define a method that Vue will call to handle the 'update' event.
    arrowClickLeft: function() {
      this.model = this.model - 1
    },
    arrowClickRight: function() {
      this.model = this.model + 1
    }
  }
};
</script>